<template>
  <div class="main">
    <div class="fixed">
      <p class="createTaskP">
        {{
          this.wantCreate
            ? "you can this (;"
            : "Create a new task? (GLOBAL) And Anonymous"
        }}
      </p>
      <button @click="wantToCreate()" class="this-button">
        {{ this.wantCreate ? "Hide" : "Yes, i want to create" }}
      </button>

      <Form v-if="wantCreate"></Form>

      <button @click="tasksVisibleOrNo" class="showTasksButton">
        {{
          this.showingTasks ? "Hide Universal Tasks" : "Show Universal Tasks"
        }}
      </button>
    </div>

    <div class="allTasks">
      <div
        v-if="showingTasks"
        class="tasks"
        v-for="task in tasks"
        :key="task._id">
        <div class="div-title">
          <p class="p-title">T:</p>
          <p>{{ task.title }}</p>
        </div>

        <div class="div-description">
          <p class="p-description">
            {{ task.description }}
          </p>
        </div>

        <div class="div-date">
          <p class="p-date">{{ task.deadline }}</p>
        </div>
      </div>
    </div>
  </div>

  <div class="emptySp"></div>
</template>

<script>
import axios from "axios";
import Form from "./Form.vue";
export default {
  data() {
    return {
      wantCreate: false,
      showingTasks: false,
      baseUrl: "https://task-api-node.onrender.com/",
      tasks: [],
    };
  },
  mounted() {
    this.getTasks();
  },
  components: {
    Form,
  },
  methods: {
    wantToCreate() {
      this.wantCreate = !this.wantCreate;
      this.showingTasks = false;
    },
    tasksVisibleOrNo() {
      this.showingTasks = !this.showingTasks;
      this.wantCreate = false;
    },
    getTasks() {
      axios
        .get(`${this.baseUrl}`)
        .then((res) => {
          console.log(res);
          this.tasks = res.data.reverse();
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style>
.p-date {
  font-size: 12px;
}

.div-title {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.div-description {
  width: 250px;
}

.div-date {
  display: flex;
  align-items: end;
  justify-content: end;
}
.allTasks {
  margin-top: 400px;
}
.fixed {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  gap: 0.5rem;
}
.main {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.this-button {
  border-radius: 2px;
  color: black;
  margin-top: 30px;
  transition: 0.4s;
  outline: none;
  border: none;
  cursor: pointer;
  background: rgb(255, 204, 0);
  padding: 5px;
}

.this-button:hover {
  transition: 0.4s;
  background: black;
  color: white;
}

.createTaskP {
  text-align: center;
  margin-top: 200px;
  font-size: 22px;
  font-weight: bold;
  color: rgb(177, 176, 176);
  animation: animation 5s ease-out infinite;
}

.showTasksButton {
  margin-top: 5px;
}

.tasks {
  border: 1px solid white;
  color: white;
  overflow: auto;
  padding: 10px;
  border-radius: 10px;
  border-bottom: 3px solid gray;
  display: flex;
  justify-content: space-between;
  width: 700px;
  gap: 1rem;
  align-items: center;
}

.p-title,
.p-date {
  color: rgb(217, 255, 0);
  font-weight: bold;
  font-style: italic;
}

.p-date {
  width: max-content;
}

.p-description {
  font-size: 13px;
  word-break: break-all;
  overflow: auto;
  width: 200px;
  color: rgb(172, 172, 172);
}

img {
  filter: invert();
  width: 25px;
  height: 25px;
  object-fit: cover;
}

.emptySp {
  margin-bottom: 200px;
}

@keyframes animation {
  0% {
    opacity: 0.8;
  }

  50% {
    opacity: 0.3;
  }

  100% {
    opacity: 1;
  }
}

@media (max-width: 900px) {
  .tasks {
    width: 100vw;
  }

  .p-description {
    text-align: center;
    overflow: auto;
    max-width: 300px;
    max-height: 400px;
  }
}
</style>
