<template>
    <form @submit.prevent="formSend()">
        <input type="text" placeholder="Title of your task" v-model="title">
        <textarea placeholder="Description" v-model="description"></textarea>
        <input type="date" placeholder="DeadLine" v-model="deadline">
        <button type="submit" class="btn-submit">It's OK</button>
        <p v-if="SomethingWrong" class="somethingWrong">{{SomethingWrong}}</p>
    </form>
    <div class="taskCreated" v-if="taskCreated">
      <p>Task Created</p>
    </div>
</template>


<script>
import axios from "axios"
export default {
  name: "Form",
  data() {
   return {
    title: "",
    description: "",
    deadline: "",
    taskCreated: "",
    SomethingWrong: false,
    allOk: true,
    baseURL: "https://task-api-node.onrender.com/"
   }
  },
  methods: {
    formSend() {
      this.allOk = true

      this.validations()
      if(this.allOk === false) 
      return


      const data = {
        title: this.title,
        description: this.description,
        deadline: this.deadline
      }
      axios.post(`${this.baseURL}`, data) 
      .then((res) => {
        console.log(res)
        this.taskCreated = "Task Created."
        setTimeout(() => {
          window.location.reload()
        }, 1200)
      })
   
    },

    validations() {
      if(this.title === "" || this.title === null || this.title === "undefined"
        || this.description === "" || this.description === null || this.description === "undefined"
        || this.deadline === "" || this.deadline === null || this.deadline === "undefined"
      ) {
        this.SomethingWrong = "Check all fields"
        return this.allOk = false 
      } 

      if(this.title.length >= 12) {
        this.SomethingWrong = "The title is too big"
        return this.allOk = false 
      }

      if(this.description.length >= 70) {
        this.SomethingWrong = "The description is too big"
        return this.allOk = false 
      }
      return this.allOk = true

    }
  }
}
</script>


<style>

    .btn-submit {
      background: rgb(59, 97, 2);
    }
    
    form {
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
      margin-top: 20px;
      text-align: center;
    }

    input {
      padding: 4px;
      outline: none;
      border: none;
      border-radius: 4px;
    }

    button {
      border-radius: 2px;
      transition: 0.5s;
      cursor: pointer;
      color: white;
      background: rgb(83, 30, 153);
      padding: 4px;
      border: none;
      outline: none;
    }

    button:hover {
      transition: 0.5s;
      opacity: 0.4;
    }

    textarea {
      resize: none;
    }

    .taskCreated {
      color: greenyellow;
      animation: task 1s linear infinite;
    }

    .somethingWrong {
      font-size: 12px;
      color: red;
    }

    @keyframes task {
      0% {
        opacity: 0.3;
      }
      50% {
        color: yellow;
        opacity: 0.6;
      }
      100% {
        opacity: 1;
      }
    }
</style>